import React, { useEffect } from 'react'
import form from '../scss/form.module.css'
import { useForm, useFieldArray, Controller } from "react-hook-form";

function Form(props) {
    const { 
        register, 
        handleSubmit,
        reset,
        setError,
        formState,
        formState:{isSubmitting, errors, isSubmitSuccessful},
    } = useForm({ defaultValues: { name:"", num:"", date:"" }});
    const onSubmit = data =>  alert(`. ${data.name}!  !!`);

    React.useEffect(() => {
        if(formState.isSubmitSuccessful){
            reset({name:"", num:"", date:""});
        }
    }, [formState, isSubmitSuccessful, reset])

    useEffect(() => {
        let errorMessage = "";

        if (errors.name){
            errorMessage += errors.name.message;
        }
        if (errors.num){
            errorMessage += errors.num.message;
        }
        if (errors.date){
            errorMessage += errors.date.message;
        }

        if (errorMessage !== ""){
            alert(`${errorMessage} `);
        }
    })

  return (
    <section id={props.id} className={`${form.sectionbg}`}>
        <div className={`subtop ${form.subtop}`}>
            <p>   <em></em></p>
            <a href="" className={`${form.subtopbtn}`}>
                <span>GO</span>
                <div className='arrow'></div>
            </a>
        </div>
        <div className={`${form.title}`}>
            <p className={`${form.subtitle}`}>  X  </p>
            <p className={`${form.maintitle}`}>   <br />   !!</p>
        </div>
        <form 
        method='GET'
        action='/server'
        onSubmit={handleSubmit(onSubmit)}
        className={`${form.formBox} d-flex flex-column flex-sm-row position-relative`}
        >
            <div className={`${form.mainform}`}>
                <div className={`col-12`}>
                    <input
                    type="text"
                    name="name"
                    placeholder=""
                    {...register("name", {
                        required:","
                    })}
                    aria-invalid={errors.name ? "true" : "false"}
                    className={`col-5`}
                    />
                    <input
                    type="tel"
                    name="num"
                    placeholder=""
                    {...register("num", {
                        required: ","
                    })}
                    aria-invalid={errors.num ? "true" : "false"}
                    className={`col-7`}
                    />
                </div>
                <input
                type="date"
                name="date"
                placeholder=""
                className={`w-100 col-12`}
                {...register("date", {
                    required: ""
                })}
                aria-invalid={errors.date ? "true" : "false"}
                />
            </div>
            <input 
                type="submit" 
                value="" 
                disabled={isSubmitting}
                className={`${form.submitbtn}`}
            />
            <div className={`${form.notice}`}>
                <p>* 1  .</p>
            </div>
        </form>
    </section>
  )
}

export default Form